import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_DELETE_ROWS = Symbol('TABLE_DELETE_ROWS');

export function deleteTableRows(table) {
  return {
    type: TABLE_DELETE_ROWS,
    payload: {
      node: table,
    },
  };
}

interface TableDeleteRowsPayload {
  node: Table;
}

interface TableDeleteRowsAction {
  type: typeof TABLE_DELETE_ROWS;
  payload: TableDeleteRowsPayload;
}

export function isTableDeleteRowsAction(
  action: Action,
): action is TableDeleteRowsAction {
  return action.type === TABLE_DELETE_ROWS;
}